.greetingForm {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: rgba(0, 0, 0, 0.5);

}

.inputAndButtonContainer {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
  /* padding: 5px 24px;*/
    gap: 10px;
    height: 36px;
}

.error {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #CC0000;
}

.input {
    border: 1px solid #D1D1D1;
    border-radius: 5px;

}

.input:focus {

}

.errorInput {
    border: 1px solid #CC0000;
    border-radius: 5px;
}

.button {
    width: 78px;
    background: #0066CC;
    border-radius: 3px;
    color: #FFFFFF;
}

.button:focus {
}

.button:active {
    width: 78px;
    background: #0066CC;
    border-radius: 3px;
}

.button:disabled {
    width: 78px;
    background: #0066CC;
    opacity: 0.5;
    border-radius: 3px;
}

.text {
}

.greeting {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
}
